$mockBackground: #F1F1F1;

.project-component {

  display: flex;
  flex-direction: column;
  gap: 1rem;

    &-hero {
      background: $mockBackground;
      width: 100%;
      min-height: 320px;
      border-radius: 20px;
    }

  &-about {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    font-family: var(--primary-font-family);
    color: var(--dark-color);
    font-size: 3.75rem;

    .about-title {
          font-family: var(--primary-font-family);
          color: var(--dark-color);
          font-size: 3.75rem;
    }

        .about-text {
          font-family: var(--primary-font-family);
          color: var(--dark-color);
          font-size: 1.5rem;
          font-weight: 200;
          line-height: 2.0625rem;
    }

  }

  .text-content {
              font-family: var(--primary-font-family);
          color: var(--dark-color);
          font-size: 1.5rem;
          font-weight: 200;
          line-height: 2.0625rem;

  }

}
