$image-backdrop: #D9D9D9;

.project-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  cursor: pointer;

  &-image {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 20px;
    background: $image-backdrop;
  }

  &-name {
        font-family: var(--primary-font-family);;
    font-style: normal;
    font-weight: 400;
    font-size: 1.5rem;
    line-height: 2.06rem;
    color: var(--dark-color);
    margin: 0;
  }

  &-category {
        font-family: var(--primary-font-family);;
    font-style: normal;
    font-weight: 400;
    font-size: 1.5rem;
    line-height: 2.06rem;
    color: var(--secondary-dark);
    margin: 0;

  }
}
