@import './reset.css';

:root {
 --dark-color: #1D1D1D;
 --secondary-dark: #717171;
 --primary-font-family: 'Manrope';
 --accent-color: #9A18DE;
}

html,
body,
#root {
    // height: 100%;
}

.my-awesome-portfolio{
  height: 100%;
  font-family: var(--primary-font-family);
}

.logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: .5rem;

  &-circle {
    width: 31px;
    height: 31px;
    background: var(--accent-color);
    border-radius: 100%;
  }

  &-link {
    text-decoration: none;
    color: var(--dark-color);

    &:active {

    color: var(--dark-color);
    }

  }
}

.container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.menu{
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0px;
    gap: 10px;
li {
    display: inline-block;
    margin-right: 10px;
a {
    text-decoration: none;
    padding: 5px 10px;
  font-family: var(--primary-font-family);
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: var(--dark-color);
    transition: .25s all;

      &:hover {
    color: var(--accent-color);
  }
  }
  }
}

.logo-menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 35px;
    padding: 0 20px;
    max-width: 1200px;
    margin: 35px auto;
}


